@charset "UTF-8";
/*
 * a set of opinions about
 * default HTML styling
 */
* {
  box-sizing: border-box; }

html,
body {
  /*
 * stops ugly white backgrounds
 * when the content isn't as tall as window
 */
  height: 100%;
  margin: 0;
  padding: 0; }

.clear:before, .row:before, .clear:after, .row:after {
  content: " ";
  display: table; }

.clear:after, .row:after {
  clear: both; }

/*
 * some default link styling
 */
a:not([class]),
.link {
  color: #1e9bd6;
  transition: opacity 0.2s ease, color 0.2s ease, border 0.2s ease;
  border-bottom: 1px solid transparent; }

a:hover:not([class]),
a:focus:not([class]),
.link:hover,
.link:focus {
  opacity: 0.8;
  border-bottom-color: rgba(30, 155, 214, 0.5); }

a:visited:not([class]),
.link:visited {
  color: #5930d6; }

.hidden {
  clip: rect(1px, 1px, 1px, 1px);
  overflow: hidden;
  position: fixed; }

/*
