  width: 8.33333%;
  padding-left: 0.5rem;
  padding-right: 0.5rem; }
  .col:not([class*="float-"]),
  .cols:not([class*="float-"]) {
    float: left; }
    .col:not([class*="float-"]):last-child:not(:first-child),
    .cols:not([class*="float-"]):last-child:not(:first-child) {
      float: right; }
  .col.row:not([class*="pad-"]),
  .cols.row:not([class*="pad-"]) {
    padding-left: 1rem;
    padding-right: 1rem; }

.center,
.centered {
  float: none;
